import React from "react";
// import { Container } from './styles';

function CVMoiNhat() {
  return (
    <>
      <section className="page-section portfolio text-white bg-primary">
        <div className="container">
          {/* Portfolio Section Heading*/}
          <h2 className="page-section-heading text-center text-uppercase text-white mb-0">
            công việc mới nhất
          </h2>
          {/* Icon Divider*/}
          <div className="divider-custom divider-light">
            <div className="divider-custom-line"></div>
            <div className="divider-custom-icon">
              <i className="fas fa-star"></i>
            </div>
            <div className="divider-custom-line"></div>
          </div>
          {/* Portfolio Grid Items*/}
          <div className="row">
            {/* Portfolio Item 1*/}
            <div className="col-md-6 col-lg-4 mb-5">
              <div
                className="portfolio-item mx-auto"
                data-toggle="modal"
                data-target="#portfolioModal1"
              >
                <img
                  className="img-fluid"
                  src="assets/img/portfolio/cabin.png"
                  alt=""
                />
              </div>
            </div>
            {/* Portfolio Item 2*/}
            <div className="col-md-6 col-lg-4 mb-5">
              <div
                className="portfolio-item mx-auto"
                data-toggle="modal"
                data-target="#portfolioModal2"
              >
                <img
                  className="img-fluid"
                  src="assets/img/portfolio/cake.png"
                  alt=""
                />
              </div>
            </div>
            {/* Portfolio Item 3*/}
            <div className="col-md-6 col-lg-4 mb-5">
              <div
                className="portfolio-item mx-auto"
                data-toggle="modal"
                data-target="#portfolioModal3"
              >
                <img
                  className="img-fluid"
                  src="assets/img/portfolio/circus.png"
                  alt=""
                />
              </div>
            </div>
            {/* Portfolio Item 4*/}
            <div className="col-md-6 col-lg-4 mb-5 mb-lg-0">
              <div
                className="portfolio-item mx-auto"
                data-toggle="modal"
                data-target="#portfolioModal4"
              >
                <img
                  className="img-fluid"
                  src="assets/img/portfolio/game.png"
                  alt=""
                />
              </div>
            </div>
            {/* Portfolio Item 5*/}
            <div className="col-md-6 col-lg-4 mb-5 mb-md-0">
              <div
                className="portfolio-item mx-auto"
                data-toggle="modal"
                data-target="#portfolioModal5"
              >
                <img
                  className="img-fluid"
                  src="assets/img/portfolio/safe.png"
                  alt=""
                />
              </div>
            </div>
            {/* Portfolio Item 6*/}
            <div className="col-md-6 col-lg-4">
              <div
                className="portfolio-item mx-auto"
                data-toggle="modal"
                data-target="#portfolioModal6"
              >
                <img
                  className="img-fluid"
                  src="assets/img/portfolio/submarine.png"
                  alt=""
                />
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default CVMoiNhat;
